:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

html {
  box-sizing: border-box;
}

li {
  list-style: none;
}

/*logo*/

.api-logo {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  justify-content: center;
  max-width: 160px;
  top: 10px;
}

.api-logo__img {
  width: 100%;
}

/*btns*/

.btn {
  text-align: center;
  align-items: center;
  cursor: pointer;

  color: #636363;
  background: rgba(0, 0, 0, 0.1);

  transition: 0.25s;
  border: 1px solid rgba(0, 0, 0, 0.4);
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}

.btn:hover {
  background: rgba(0, 0, 0, 0.4);
  color: #fdfdfd;
}

.sidelong {
  display: block;
  position: fixed;
  width: 38px;
  height: 30px;
  border-radius: 6px;
  z-index: 900;
  left: 8px;
  opacity: 1;
}

.back-to-top {
  bottom: auto;
  top: 40%;
}

.load-more {
  top: auto;
  bottom: 35%;
}

/*gallery*/
.gallery {
  display: grid;
  width: 1200px;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  grid-auto-rows: 240px;
  grid-gap: 12px;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.img-card {
  position: relative;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  display: block;
  text-decoration: none;
  height: 100%;
}

.gallery__link:hover {
  cursor: zoom-in;
}

/*search-form*/

.search-form {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 350px;
}

.search-form input {
  position: relative;
  width: 100%;
}

.search-form span {
  position: absolute;
  margin-left: -22px;
  z-index: 200;
}
